.booking-container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  font-family: "Segoe UI", sans-serif;
  /* background-color: #090909; */
  /* color: #111; */
  color: #111;
  background-color: #ffffff;
}

.booking-container.full-width {
  /* for services, barber, confirm, done */
  min-height: 100vh;
  height: auto;
}

.booking-container.full-screen {
  /* for other steps */
  height: auto; /* no fixed height */
}

