/* ────────────── Responsive (Mobile + Tablet) ────────────── */
@media screen and (max-width: 992px) {
  /* .booking-container {
    height: auto; */
  /* min-height: 100vh;
    overflow-x: hidden; ⛔ removed to allow natural layout */

  /* overflow-y: visible;
    padding-bottom: 20px;
  } */

  .booking-container {
    overflow-y: visible;
    padding-bottom: 20px;
  }

  .booking-container.full-width {
    height: 100vh;
  }

  .booking-container.full-screen {
    height: auto;
  }

  .booking-header {
    position: sticky; /* makes it stick to the top */
    top: 0; /* sticks right at the top edge */
    left: 0;
    width: 100%; /* full width for proper alignment */
    z-index: 9999; /* ensure it stays above all other content */
    flex-direction: row;
    gap: 60px;
    text-align: center;
    background-color: #ffffff !important; /* match page background so it blends in */
    /* background-color: #090909; match page background so it blends in */
    padding: 10px 0; /* extra space so content isn't cramped */
    border-bottom: 1px solid #333; /* optional: subtle divider */
  }
  
  .booking-logo {
    width: 250px;
    margin-left: -25px;
    border-radius: 10% !important;
  }


  .booking-header .center-logo img {
    border-radius: 10% !important;
  }

  

  .services-structured-container {
    width: 95vw;
    padding: 20px 16px;
  }

  .inrow1,
  .bottom-row {
    flex-direction: column;
    gap: 30px;
  }

  .left-half,
  .right-half {
    width: 100%;
    flex-direction: column;
    padding: 0;
  }

  .vertical-shave-text {
    writing-mode: horizontal-tb;
    transform: none;
    width: 100%;
    border-right: none;
    /* border-bottom: 1px solid #ddd; */
    font-size: 30px;
    justify-content: flex-start;
    padding: 10px 10px;
    margin-bottom: 10px;
  }

  .vertical-shave-text span {
