.custom-slider {
  position: relative;
  width: 100%;
  /* height: 60vh !important; */
  /* max-width: 600px; */
  /* margin-top: -5% !important; */
  margin: auto;
  overflow: hidden;
  border-radius: 10px !important;
  position: relative;
}

.slides-container {
  display: flex;
  /* height: 60vh; */
  transition: transform 0.5s ease-in-out;
}

.slider-image {
  width: 100%;
  flex-shrink: 0;
  object-fit: cover;
  height: 570px;
  border-radius: 20px !important;
  /* height: 20% !important; */
}

/* Navigation buttons */
.nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(30, 30, 47, 0.7);
  border: none;
  color: white;
  font-size: 2rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  z-index: 10;
}

.prev {
  left: 10px;
  border-radius: 20px !important;
}

.next {
  right: 10px;
  border-radius: 20px !important;
}

/* Dots */
.dots {
  text-align: center;
  margin-top: 10px;
}

.dots button {
  border: none;
  background: #ccc;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
  padding: 0;
}

.dots button.active {
  background: #007bff;
}

/* Responsive */
@media (max-width: 768px) {
  .custom-slider {
  margin-top: 0% !important;
}
  .slider-image {
    height: 250px;
  }
}
