/* === Common Header Styles === */
.queue-header {
  width: 100%;
  margin-bottom: 2rem;
}

/* === DESKTOP header === */
.desktop-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

/* Desktop layout specifics */
.desktop-logo {
  width: 120px !important;
  height: 120px !important;
  object-fit: contain;
}

.desktop-nav {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex: 1;
}

.desktop-nav a {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  font-size: 1.2rem;
  transition: color 0.3s;
}

.desktop-nav a:hover {
  color: #007bff;
}

.add-btn {
  padding: 0.6rem 1.2rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  font-weight: 600;
  white-space: nowrap;
  flex-shrink: 0;
}

/* === Mobile/Tablet Top Row (logo + hamburger) === */
.top-row-header {
  display: none;
  justify-content: space-between;
  align-items: center;
}

/* Hamburger */
.hamburger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
}

.hamburger .bar {
  width: 25px;
  height: 3px;
  background-color: #333;
}

/* === Mobile Menu === */
.mobile-nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1rem;
  width: 100%;
  background-color: white;
  padding: 1rem 0;
  border-top: 1px solid #ccc;
}

.mobile-nav a {
  padding: 10px 20px;
  font-size: 1.1rem;
  text-decoration: none;
  color: #111;
  font-weight: 600;
}

.add-btn-mobile {
  margin: 10px 20px 0;
  padding: 0.6rem 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
}

/* === Media Queries === */
@media screen and (max-width: 768px) {
  .desktop-header {
    display: none;
  }

  .top-row-header {
    width: 85%;
    display: flex;
  }

  .hamburger {
    display: flex;
  }
}




.queue-list {
  background: #fff;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  min-height: 100px;
}

.customer-card {
  background: #fdfdfd;
  border-left: 4px solid #007bff;
  padding: 1rem;
  margin-bottom: 0.8rem;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.card-index {
  font-size: 0.85rem;
  color: #666;
}

.card-triggerTime {
  font-size: 1.2rem;
  color: #666;
}

.card-name {
  font-size: 1.5rem;
  font-weight: 600;
  color: #222;
}

.card-eta {
  font-size: 1.2rem;
  font-weight: 600;
  /* color: #555; */
  color: #fc2f2f;
}

.status-green {
  font-weight: bold;
  color: #28a745;
}

.status-red {
  font-weight: bold;
  color: #dc3545;
}

.status-blue {
  font-weight: 600;
  color: #007bff;
}

.card-actions {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  align-items: flex-end;
}

.delete-btn {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.2rem;
}

