/* Highlight current customer */
.customer-card.current-customer {
  background-color: #04c71b; /* bright green */
  color: #fff;               /* white text */
  font-weight: bold;
  border: 2px solid #025002;
}

.customer-card.current-customer .card-name,
.customer-card.current-customer .card-eta,
.customer-card.current-customer .status-green,
.customer-card.current-customer .card-triggerTime,
.customer-card.current-customer .card-index {
  color: #fff !important;
}


