.break-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.minus-btn,
.plus-btn {
  border: none;
  padding: 0.3rem 0.6rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
}

.minus-btn {
  background-color: #17a2b8;
  color: #979797;
}

.plus-btn {
  background-color: #ffc107;
  color: #222;
}

.apply-btn {
  margin-top: 0.5rem;
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 0.3rem 0.6rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
  width: 100%;
}

.break-time {
  font-weight: bold;
  font-size: 0.85rem;
  min-width: 80px;
  text-align: center;
}

