/* MODALS */
.modal-overlay {
  background-color: rgba(0, 0, 0, 0.6) !important;
  z-index: 1000;
}

.customer-modal,
.walkin-modal {
  width: 90%;
  max-width: 900px;
  margin: auto;
  max-height: 90vh;
  border-radius: 16px;
  padding: 2rem;
  background: #fff;
  border: none;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  overflow: auto;
}

.modal-title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #222;
}

.details-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.details-grid strong {
  color: #555;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.close-btn {
  padding: 0.6rem 1.4rem;
  background-color: #6c757d;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
}


.ReactModal__Overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background-color: rgba(0, 0, 0, 0.6) !important;
  z-index: 1000 !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ReactModal__Content {
  position: relative !important;
  inset: auto !important;
  margin: auto;
  max-height: 90vh;
  overflow-y: auto;
  z-index: 1001 !important;
}


.break-wrapper {
  position: relative;
  display: inline-block;
}

.break-btn {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  padding: 1rem 2rem;
  font-size: 1.2rem;
}

.break-popup {
  position: absolute;
  top: 110%;
  right: 0;
  width: 200px;
  background: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 12px;
  z-index: 999;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.15);
}

.break-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.minus-btn,
.plus-btn {
  padding: 6px 10px;
  font-size: 14px;
  background-color: #f0f0f0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.break-time {
  font-weight: 600;
  font-size: 14px;
  margin: 0 8px;
  color: #333;
}

.apply-btn {
  width: 100%;
  background-color: #28a745;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 8px;
  font-size: 14px;
}

.break-total {
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  color: #555;
}



/* ========== RESPONSIVE STYLES ========== */

/* Tablets */
@media screen and (max-width: 1024px) {
  .queue-wrapper {
    width: 95%;
    padding: 1rem;
  }

  .queue-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .queue-header h2 {
    font-size: 1.5rem;
  }

  .add-btn {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }

  .customer-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .card-actions {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  .details-grid {
    grid-template-columns: 1fr;
  }

  .modal-title {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 768px) {
  .queue-header {
    flex-direction: column;
    align-items: center;
  }

  .left-header {
    flex-direction: column;
    align-items: center;
  }

  .nav-links {
    justify-content: center;
  }

  .add-btn {
    margin-top: 0.5rem;
  }

  
  .customer-modal,
  .walkin-modal {
    width: 75% !important;
    height: 70%;
    padding: 1rem;
  }
}



/* Mobile */
@media screen and (max-width: 600px) {
  .queue-container {
    padding: 1rem;
  }

  .queue-wrapper {
    width: 100%;
    padding: 0;
  }

  .queue-header h2 {
    font-size: 1.3rem;
  }

  .add-btn {
    font-size: 0.85rem;
    padding: 0.5rem 0.9rem;
  }

  .queue-list {
    padding: 1rem;
    width: 80vw !important;
  }

  .customer-card {
    padding: 0.8rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .card-name {
    font-size: 1rem;
  }

  .card-eta {
    font-size: 0.85rem;
  }

  .card-actions {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    gap: 0.5rem;
  }

  .delete-btn,
  .break-btn {
    font-size: 0.75rem;
    padding: 0.4rem 0.6rem;
  }

  .modal-title {
    font-size: 1.3rem;
  }

  .customer-modal,
  .walkin-modal {
    width: 95%;
    padding: 1rem;
  }

  .break-popup {
    right: auto;
    left: 0;
    width: 100%;
    max-width: 240px;
  }

  .break-controls {
    flex-direction: column;
    gap: 0.3rem;
  }

  .break-time {
    margin: 0.3rem 0;
  }

  .apply-btn {
    font-size: 0.75rem;
  }

  .break-total {
    text-align: center;
    font-size: 0.75rem;
  }
}
