.footer {
  width: 100vw; /* full viewport width */
  overflow-x: hidden; /* prevent horizontal scroll */
  padding-top: 40px;
  /* background-color: #000000; */
  background-color: #edebeb;
  color: #000000;
  color: #ffffff;
  font-family: 'Segoe UI', sans-serif;
  box-sizing: border-box;
    border-top: 2px solid #c5c4c4;
}


.footer-container {
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* justify-content: space-between; */
  /* border-bottom: 1px solid #ededed; */
  padding-bottom: 30px;
  box-sizing: border-box;
}

.footer-section {
  flex: 1 1 250px;
  /* margin-bottom: 30px; */
  align-content: center;
}

.footer-logo {
  height: 100px;
  margin-bottom: 12px;
}

/* .footer-tagline {
  font-size: 24px;
  font-weight: 600;
  color: #faf9f9; */
  /* color: #1e1e1e; */
/* } */

.footer-section h4 {
  /* margin-bottom: 12px; */
  font-size: 30px;
  font-weight: bold;
  /* color: #000000; */
  color: #ffffff;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section li {
  /* margin-bottom: 8px; */
  font-size: 18px;
  /* color: #090909; */
  color: #ffffff;
  font-weight: 500;
}

.footer-section a {
  color: #2d2c2c;
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-section a:hover {
  color: #545252;
}

.footer-bottom {
    border-top: 1px solid #616161;
  text-align: center;
  font-size: 13px;
  /* color: #ffffff; */
  color: #000000 !important;
  padding: 15px 20px;
  /* background-color: #000000; */
  background-color: #ffffff;
}

.footer-bottom p {
    /* color: #ffffff; */
    color: #000000;
    font-size: 1rem;
    font-weight: 500;
}
.footer-bottom p a{
    color: #000000;
    /* color: #ffffff; */
    text-decoration: none;
}

.footer-bottom2 {
    /* border-top: 1px solid #616161; */
  text-align: center;
  font-size: 13px;
  /* color: #ffffff; */
  color: #888;
  padding: -5px 15px 20px 20px;
  /* background-color: #000000; */
  background-color: #edebeb;
}

.footer-bottom2 p {
    /* color: #ffffff; */
    color: #000000;
    font-size: 1.8rem;
    font-weight: 500;
}
.footer-bottom2 p a{
    /* color: #000000; */
    color: #ffffff;
    text-decoration: none;
}



.footer-social {
  display: flex;
  gap: 12px;
  margin-top: 15px;
  justify-content: center;
}

.footer-social a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  /* background-color: #ffffff;  */
  background-color: #007BFF; 
  /* background-color: #000;  */
  color: #ffffff;
  /* color: #000000; */
  /* color: #fff; */
  font-size: 16px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.footer-social a:hover {
  /* background-color: #555; */
  background-color: #c7c3c3;
  color: #007BFF;
  border: 1px solid #007BFF;
  /* color: #706e6e; */
  transform: scale(1.1);
}


/* Responsive */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    text-align: center;
    gap: 20px;
  }

  
.footer-logo {
  height: 100px !important;
  margin-bottom: -12px;
}

  .footer-section {
    margin-bottom: -220px;
  }

  .footer-logo {
    /* margin: 0 auto 12px; */
    margin: 0 auto 0;
  }

    .footer-social {
    justify-content: center;
    margin-top: -90px !important;
  }


  
.footer-bottom2 {
    /* border-top: 1px solid #616161; */
  text-align: center;
  font-size: 13px;
  /* color: #ffffff; */
  color: #888;
  padding: 15px 20px;
  background-color: #edebeb;
  /* background-color: #000000; */
  /* background-color: #e2e0e0; */
}

.footer-bottom2 p {
    /* color: #ffffff; */
    color: #000000;
    font-size: 1.4rem;
    font-weight: 500;
}
.footer-bottom2 p a{
    /* color: #000000; */
    color: #ffffff;
    text-decoration: none;
}
}